
.nav {
   width: 100%;
   height: 100%;
   display: flex;
   justify-content: space-between;
   align-items: center;
   padding: 20px;
   margin: auto;
}

 .logo {
   width: 50px;
   height: 50px;
   border-radius: 50%;
}

@media screen and (min-width: 680px) {
   .logo {
   width: 62px;
   height: 62px;
   border-radius: 50%;
}
   .nav {
   width: 100%;
   height: 100%;
   display: flex;
   justify-content: space-between;
   align-items: center;
   padding: 20px;
}
}

.Right-side {
   width: auto;
   display: flex;
   justify-content: space-between;
}

@media screen and (min-width: 680px) {
  .Right-side {
   width: 40%;
   display: flex;
   justify-content: space-between;
}
}

input {
   width: 12em;
   height: 37px;
   font-size: 16px;
   padding-left: 6px;
   border: none;
   outline: none;
}

@media screen and (min-width: 680px) {
   input {
   width: 23em;
   height: 44px;
   font-size: 19px;
   padding-left: 6px;
   border: none;
   outline: none;
}
}

.icon {
      padding: 0px 10px 0px 0px;
   }

@media screen and (min-width: 690px) {
   .icon {
      padding: 0px;
   }
}

.count {
   display: flex;
   justify-content: center;
   align-items: center;
   width: 20px;
   height: 20px;
   border-radius: 10px;
   position: absolute;
   color: white;
   top: 2px;
   background-color: blue;
}